import React, { useState } from 'react';
import Header from "./header";
import Main from "./main";

export default function Container(){

    const [isAllVaild, setIsAllVaild] = useState(window.matchMedia('(prefers-color-scheme: light)'));

    return(
        <div className={`bg ${isAllVaild.matches ? 'lightMode' : 'darkMode'}`}>
            <div className="container">
                <Header />
                <Main />
            </div>
        </div>
    )
}